.chiriqui
    width: 100%
    height: 160vh
    background: url('../assets/Chiriqui.png')
    background-position: center
    background-size: cover
    display: flex
    justify-content: center
    align-items: center
    +smartphone
        height: 90vh

    .chiriqui__text
        font-size: 6rem
        font-weight: 600
        text-align: center
        color: white
        letter-spacing: 3px
        line-height: 10rem
        +tablet
            font-size: 4rem
            line-height: 7rem
        +smartphone
            font-size: 3.5rem
            line-height: 6rem